import React, { useState } from 'react'
import Label from './componentsLabel/componentsLabel';
import Title from './componentsTitle/componentsTitle';
import {  Link , useNavigate } from "react-router-dom";
import Input from './componentsInput/componentsInput';
import axios from "axios";
import Alls from './componentsAll/componentsAll';

const Components = () =>{
    const [email, setEmail] = useState("");
    const [contrasea, setContrasea] = useState("");
    const [logeado, setLogeado] = useState(false);
    const [user, setUser] = useState([]);

    const navegar = useNavigate()
    
    const consumeLogin = async () => {
        const respuesta = await axios.post(url, {email: email , password: contrasea }).then(respuesta => {
            console.log (respuesta.data.user) // validacion de usuario
            setUser(respuesta.data.user)
            localStorage.setItem("user", JSON.stringify(respuesta.data.user))
            setLogeado(true)
            navegar("/all", {replace:true})
            
        }).catch(error => {
            console.log(error)
            setLogeado(false)
        })
    }
    const url = "https://flink-web-test.herokuapp.com/api/v1/login"
    function handleChange(name, value){
        if (name === "email"){
            setEmail(value)
        }
        if (name === "contrasea"){
            setContrasea(value)
        }
    }

    function handleSubmit(e){
        e.preventDefault()
        let account={ email, contrasea}
        if(account){
            consumeLogin()

            console.log("account:", account)
        }
    }

    console.log ("usuario:", email)
    console.log ("contrasea:", contrasea)
    return(
        <>
        
        <div className="base">  
            <div className="container">  
                <div className="textoLogin justify-content-md-center">
                    <br />
                    <form>
                        <Title text="Inicia Sesion" />
                        <div className="form-group">
                            <Label text="Email" />
                            <Input 
                            attribute={{
                                id: "email",
                                name: "email",
                                type: "text",
                                placeholder:"Ingresa tu Email"
                            }}
                            handleChange={handleChange}
                            />
                        </div>
                        <div className="form-group">
                        <Label text="Contrasea" />
                        <Input 
                            attribute={{
                                id: "contrasea",
                                name: "contrasea",
                                type: "password",
                                placeholder:"Ingresa tu contrasea"
                            }}
                            handleChange={handleChange}
                            />
                        </div>
                        <div className="form-group form-check">
                        </div>
                        <button onClick={handleSubmit} type="submit" className="btn entrar btn-primary">Iniciar Sesion</button>
                        <br />
                        
                        <Link className="btn entrar btn-primary" to="/registro">Registro</Link>
                    </form>
                    <br />
                </div>
            </div>
        </div> 
        </>
    )
};



export default Components;